body {
  background-color: rgb(29, 31, 31);
  color: white;
}

.header {
  font-family: "Righteous", cursive;
  font-size: 40px;
  text-align: center;
  color: rgb(255, 255, 255);
  display: flex;
  justify-content: center;
}

.flex_togo{
    margin-top: 10px;
    display: flex;
    justify-content:space-between;
}
.to_vniz {
  position: fixed;
  bottom: 0;
  width: 95%;
}

.fleks {
  display: flex;
  justify-content: center;
}

.log_out {
  cursor: pointer;
  text-decoration: underline;
}

.log_out:hover {
  color: rgba(110, 252, 88, 0.856);
}

@-webkit-keyframes sendAnimation {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  50% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes sendAnimation {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  50% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
.cahat_massages {
    margin-bottom: 60px;
}
button[type="submit"] {
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

button[type="submit"]:focus {
  outline: none;
}

button[type="submit"]:active {
  -webkit-animation-name: sendAnimation;
          animation-name: sendAnimation;
}

@media screen and (max-width: 600px) {
  body {
    background-size: 55%;
  }
}

